html {
    scroll-behavior: smooth;
    font-family: Verdana, "Geneva", Tahoma, sans-serif;
}

.main {
    scroll-behavior: smooth;
    background-color: #1A1A1D;
    color: white;
    text-align: center;
    font-family: Verdana, "Geneva", Tahoma, sans-serif;
}

.header {
    position: relative;
    /* background-color: #4E4E50; */
    width: 100%;
    height: 38vh;
    overflow: hidden;
}

.header .text {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    color: #fff;
    justify-content: center;
    margin: 10px
}

.header .text .name {
    font-size: calc(0.5rem + 2vw);
    font-weight: bolder;
    margin: 15px 0px;
}

.header .text .role {
    font-size: calc(0.75rem + 4.5vw);
    font-weight: bolder;
    background: url('./text_background.png');
    background-size: 70%;
    background-position: 50% 50%;
    -webkit-background-clip: text;
    color: rgba(0, 0, 0, 0.08);
    animation: zoomout 25s ease 500ms forwards infinite;
    -webkit-text-stroke: 1px #4E4E50;
}

.header .text .btn {
    position: relative;
    background-color: #950740;
    border-radius: 100px;
    color: #fff;
    margin-top: 10px;
    cursor: pointer;
    display: inline-block;
    padding: 7px 20px;
    text-align: center;
    text-decoration: none;
    transition: all 250ms;
    border: 0;
    font-size: calc(0.5rem + 0.6vw);
    user-select: none;
    -webkit-user-select: none;
    touch-action: manipulation;
}

.header .text .btn:hover {
    color: #fff;
    box-shadow: #950740 0 10px 20px -10px;
    transform: scale(1.05) rotate(-0.5deg);
}

.menushow
{
    display: flex;
    align-items: center;
    height: calc(12rem + 140vh);
    position: fixed;
    top: calc(-20% - 6rem);
    left: calc(1rem - 47vw);
    width:50%;
    background-color: #0d0f1b;
    overflow: hidden;
    border-radius: 45%;
    transform: scale(1);
    z-index: 2;
}

ul
{
    position: inherit;
    display: flex;
    flex-direction: column;
    display: block;
    right: calc(0.3rem + 0.5vw);
}

ul li
{
    list-style: none;
    text-align: right;
    font-size: calc(0.75rem + 1.5vw);
    color: #C3073F;
    padding: 6px 0;
}

.logo-panel
{
   margin-bottom: calc(1rem + 1vw);
}

.logo-panel img{
    position: absolute;
    top: 0;
    bottom: 6px;
}

ul li img
{
    width: calc(0.75rem + 1.5vw);
    min-width: 1rem;
    height: calc(0.75rem + 1.5vw);
    min-height: 1rem;
}

ul li a:hover
{
    cursor: pointer;
}

@keyframes zoomout {
    0% {
        background-size: 70%;
    }

    50% {
        background-size: 10%;
    }

    100% {
        background-size: 70%;
    }
}

@media screen and (max-width: 645px) {
    .header {
        position: relative;
        /* background-color: #4E4E50; */
        width: 100%;
        height: 30vh;
        overflow: hidden;
    }
    
 }