h1 {
    text-align: center;
    color: #950740;
    font-size: 2vw;
    padding: 35px 0px 0px;

}

.title {
    text-align: center;
    font-size: calc(1.2rem + 1vw);
    color: #950740;
    padding: 30px 0px 0px;
    text-shadow: 1.5px 1.5px 1.5px #950740;
}

.about {
    cursor: url('../bg.jpg'), auto;
}

.about img,
.experience img,
.specailskills img,
.educational img {
    width: 97%;
    margin-left: auto;
    margin-right: auto;
    display: block;
    min-width: 200px;
}

.about .section {
    max-width: 100% important;
}

p {
    font-size: calc(0.75rem + 0.5vw);
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    color: #fff;
}

.line {
    margin-top: 40px;
}

.about .socialicons {
    position: relative;
    display: flex;
    justify-content: center;
}

.about .icons {
    width: calc(1.5rem + 1vw);
    min-width: 1rem;
    height: calc(1.5rem + 1vw);
    min-height: 1rem;
    background: #C3073F;
    padding: 3px;
    border-radius: 5px;
    color: white;
    box-shadow: -2px -2px 3px rgba(30, 161, 179, 0.2),
        3px 3px 3px rgba(0, 0, 0, 0.2);
}

.about .icons:hover {
    background: #fff;
    cursor: pointer;
    color: #C3073F;
    transition: all 0.7s ease-out;
}

/* Educational */
.educational {
    justify-content: center;
}

.rb-container {
    display: block;
    position: relative;
    margin: auto;
    width: 83%;
}

.rb-container ul.rb li {
    list-style: none;
    min-height: 50px;
    border-left: 1px dashed #fff;
    padding: 0 0 10px 30px;
    position: relative;
}

.rb-container ul.rb li:last-child {
    border-left: 0;
}

.rb-container ul.rb li::before {
    position: absolute;
    left: -8px;
    top: -5px;
    content: " ";
    border: 4px solid rgba(255, 255, 255, 1);
    border-radius: 500%;
    background: #C3073F;
    height: calc(0.80rem + 0.5vh);
    width: calc(0.80rem + 0.5vh);
    transition: all 500ms ease-in-out;
}

.rb-container ul.rb li:hover:before {
    background: #C3073F;
    border-color: #1A1A1D;
    transition: all 1000ms ease-in-out;
    cursor: pointer;
}

ul.rb li .content {
    color: #fff;
    position: relative;
    text-align: left;
    margin: 0;
    padding-top: -2%;
}

ul.rb li .content h5 {
    font-size: calc(0.65rem + 0.5vw);
    /* font-size: calc(0.75rem + 0.5vw); */
}

ul.rb li .content .course {
    font-size: calc(0.65rem + 0.5vw);
    color: #950740;
    text-shadow: 1px 1px 1px #950740;
}

ul.rb li .content .course span {
    padding-left: 17%;
}


ul.rb li .content .institute {
    font-size: calc(0.65rem + 0.5vw);
    color: #fff;
    text-shadow: 1px 1px 1px #950740;
}

.socialicons ul,
.hobbies ul {
    justify-content: center;
    text-align: right;
}

/* specailskills */
.specailskills li {
    font-size: calc(0.75rem + 0.5vw);
    color: #fff;
    text-align: left;
}

.specailskills li img {
    height: 40px;
    height: 30px;
}

.specailskills .icons {
    width: 20px;
    height: 20px;
    margin: 10px;
    color: #fff;
    border: 0.5px solid #C3073F;
    /* background-color: #C3073F; */
    border-radius: 50%;
}

.specailskills .icons:hover {
    border: 1px solid rgba(255, 255, 255, 1);
    border-radius: 50%;
    color: #C3073F;
}

.bulletin {
    margin: 0 calc(4rem + 2vw);
    /* line-height: 0.5rem; */
}

.iconsize {
    padding: 0 calc(1rem + 1vw);
    line-height: 1rem;
}

/* Projects */
.projects {
    color: #fff;
    /* padding-left: 7%; */
}

.projects .site-image {
    background-color: #950740;
    padding: 1px;
    width: 100%;
    height: auto;
    margin-top: 8%;
}

.projects .title {
    padding-bottom: 3px;
}

.projects .title img {
    width: 34px;
    /* padding: 2px; */
}

.projects h3 {
    color: #950740;
    text-align: center;
    font-size: calc(1rem + 1vw);
    text-shadow: 1.1px 1.1px 1.1px #950740;
}

.projects .btn {
    position: relative;
    background-color: #950740;
    border-radius: 100px;
    color: #fff;
    margin-top: 20px;
    cursor: pointer;
    display: inline-block;
    padding: 7px 20px;
    text-align: center;
    text-decoration: none;
    transition: all 250ms;
    border: 0;
    font-size: calc(0.5rem + 0.6vw);
    user-select: none;
    -webkit-user-select: none;
    touch-action: manipulation;
}

.projects .btn:hover {
    color: #fff;
    box-shadow: rgba(158, 15, 77, 0.136) 0 -25px 18px -14px inset, rgba(158, 15, 77, .25) 0 1px 2px, rgba(158, 15, 77, .25) 0 2px 4px, rgba(158, 15, 77, .25) 0 4px 8px, rgba(158, 15, 77, .25) 0 8px 16px, rgba(158, 15, 77, .25) 0 16px 32px;
    transform: scale(1.05) rotate(-0.5deg);
}

.projects video {
    width: 100%;
}


.hobbyprojects p {
    font-size: calc(0.75rem + 0.5vw);
}

.hobbyprojects .hobbycard {
    background-color: rgba(149, 7, 64, 0.8);
    margin: 15px;
    padding: 3px;
}

.hobbyprojects .hobbycard h3 {
    color: #fff;
    font-size: calc(1.2rem + 0.5vw);
}

.hobbyprojects span,
.hobbies span {
    color: #fff;
    font-weight: 600;
}

/* contact */
.contact {
    display: flex;
    text-align: center;
}

.contact img {
    width: 50px;
    padding-bottom: 5px;
}

/* Hobbies */
.hobbies img {
    width: 50%;
}

.hobbies .quote {
    font-size: calc(0.75rem + 0.5vw);
    letter-spacing: 2px;
    color: gray;
}

.hobbies li {
    font-size: calc(0.75rem + 0.5vw);
    color: #fff;
    letter-spacing: 0.5px;
}

.hobbies p {
    font-size: calc(0.75rem + 0.5vw);
}

.hobbies .quote:hover {
    text-shadow: 3px 3px rgba(27, 30, 33, 0.3);
}

.hobbies .topic {
    color: #950740;
    font-size: calc(1rem + 0.5vw);
    /* padding: 10px 0px; */
    text-align: center;
}

.hobbies .achievments img {
    width: 30px;
}

.hobbies .icons {
    width: 23px;
    height: 23px;
    margin: 6px;
    color: #fff;
    border-radius: 50%;
    cursor: pointer;
}

/* FOOTER */
.footer {
    left: 0;
    bottom: 0;
    margin: 0;
    padding: 0;
    width: 100%;
    color: white;
    background-color: #0d0f1b;
    text-align: center;
}

.footer h5 {
    font-size: calc(1rem + 0.5vw);
}

.footer h4 {
    font-size: calc(1rem + 0.5vw);
    line-height: 33px;
}


.footer a {
    color: white;
}

.footer a:hover {
    color: white;
}

@media screen and (max-width: 770px) {
    .about img,
    .experience img,
    .specailskills img,
    .educational img {
        width: 50%;
        margin-left: auto;
        margin-right: auto;
        display: block;
        min-width: 200px;
    }

    .projects {
        color: #fff;
        padding-left: 7%;
    }

    .educational{
            padding-left: 7%;
    }
    
 }